import {Injectable} from '@angular/core';
import {Moto} from './moto';
import {HdmotosrvService} from './hdmotosrv.service';

declare var moto: any;

import * as crossfilter from 'crossfilter';

@Injectable()
export class MotosrvService {

    private data: Moto[];

    private b = 0;

    constructor(private hdmotosrvService: HdmotosrvService) {
        this.initData();
        //console.log(this.data);
        //console.log(moto);
    }

    initData() {
        this.data = moto;
    }

    getData(): Moto[] {
        return this.data;
    }

    getNewData(): Moto[] {
        this.data = moto;
        return this.data;
    }

    getCf() {
        this.hdmotosrvService.setMoto(0, [10, 11, 12, 15, 16, 17], "2016-10-1", "2016-10-10");

        //var ret = this.byTmper.filter(1);         /*    var ret: any[];             for (var i = 0; i < someArray.length; _i++) {                    	var item = someArray[_i];                    	console.log(item);            	}            	*/
        return this.b;
        //return this.cf.size();
    }

    getByDate(_date: string) {
        var _cf = crossfilter(this.data);
        var _stageDate = _cf.dimension(function (d) {return d.dt;});
        _stageDate.filter(_date);
        var _ret = _stageDate.bottom(Infinity);
        var arrtmp: number[] = [0, 0, 0, 0, 0, 0];

        if (_ret.length > 0) {
            for (let i = 0; i < _ret[0].moto.length; i++) {
                for (let j = 0; j < 3; j++) {
                    switch (_ret[j].tmper) {
                        case 2:
                            arrtmp[0] = _ret[j].moto[i];
                            arrtmp[3] = _ret[j].lmoto[i];
                            break;
                        case 3:
                            arrtmp[1] = _ret[j].moto[i];
                            arrtmp[4] = _ret[j].lmoto[i];
                            break;
                        case 1:
                            arrtmp[2] = _ret[j].moto[i];
                            arrtmp[5] = _ret[j].lmoto[i];
                            break;
                    }
                }
                this.hdmotosrvService.setMoto(i, arrtmp, _ret[0].dt, "");
            }
        }
    }

    getDay(): Moto[] {
        return this.data;
    }

    getNight(): Moto[] {
        return this.data;
    }

    getSum(): Moto[] {
        return this.data;
    }

}



