import {Component, OnInit} from '@angular/core';
import {HdmotosrvService} from '../hdmotosrv.service';

@Component({
    selector: 'app-tmenu',
    templateUrl: './tmenu.component.html',
    styleUrls: ['./tmenu.component.css']
})
export class TmenuComponent implements OnInit {

    title = "Условия выборки";
    hdmotos: any[] = [];
    equips: any[] = [];

    condition: boolean = true;

    constructor(private hdmotosrvService: HdmotosrvService) {}

    chkEq(chkNmb) {
        this.hdmotosrvService.chkMot(chkNmb);
        //    console.log(this.hdmotosrvService.items);
    }

    ngOnInit() {

        var arr: any[] = [];

        //console.log(this.hdmotosrvService.getStageList());
        //this.items = this.hdmotosrvService.getDataItems();

        //console.log(this.items);

        this.hdmotos = this.hdmotosrvService.getStageList();

        /*        for (let i = 0; i < this.hdmotos.length; i++) {
                    this.hdmotosout.push(this.hdmotos[i]);
                }
        */
        this.equips = this.hdmotosrvService.getStruct();

        //this.equips[4] = this.hdmotosrvService.getEqByStage('a');

        //console.log(this.hdmotos);
        //console.log(this.equips);

        //    this.st3 = this.hdmotosrvService.getEqByStage(3);
        //    this.st4 = this.hdmotosrvService.getEqByStage(4);
        //    this.stA = this.hdmotosrvService.getEqByStage(a);
    }

    toggle() {
        this.condition = !this.condition;
    }

}



