import {
    Component, OnInit, OnDestroy, Output, EventEmitter,
    HostListener, AfterViewInit
} from '@angular/core';

import {Response} from '@angular/http';
import {HttpService} from './http.service';
import {Observable} from 'rxjs/Observable';
import {NgbPanelChangeEvent} from '@ng-bootstrap/ng-bootstrap';
import * as Rx from 'rxjs/Rx';
import {MotosrvService} from './motosrv.service';
import {HdmotosrvService} from './hdmotosrv.service';

declare var MyStorage: any;
declare var ajaxOn: boolean;
declare var moto: any;


@Component({
    selector: 'app-root',
    templateUrl: './app.component.html',
    styleUrls: ['./app.component.css']
})

export class AppComponent implements OnInit, OnDestroy, AfterViewInit {

    stlib: any;
    ajOn: any;

    subscription: any;

    constructor(private httpService: HttpService,
        private motosrvService: MotosrvService,
        private hdmotosrvService: HdmotosrvService) {

        ajaxOn = false;

        this.stlib = new MyStorage();
    }

    ngOnInit() {
        this.initData();
        jQuery(moto).on('change', () => {
            this.motosrvService.initData();
            alert('moto = ' + moto);
        });
    }

    ngOnDestroy() {
        // viewChild is set after the view has been initialized
        //        console.log("dstr");
    }

    onTab() {
        //        console.log('Tabs!!!');
    }

    ngAfterViewInit() {
    }

    getData() {
        this.stlib.save(this.motosrvService.getData());
    }

    initData() {
        var dtstr: any;
        var tmstr: any;
        var dtmstr: any;
        var _dtm: any;

        if (this.stlib.getData() == null) {
            document.getElementById("lbload").innerHTML = '--.-- --:--';
        } else {
            moto = this.stlib.getData();
            _dtm = this.stlib.getTm();

            dtstr = new Date(_dtm).toLocaleDateString("ru", {month: "2-digit", day: "2-digit"});
            tmstr = new Date(_dtm).toLocaleTimeString("ru", {hour: "2-digit", minute: "2-digit"});
            dtmstr = dtstr + ' ' + tmstr;

            document.getElementById("lbload").innerHTML = dtmstr;
            //console.log('данные есть!');
        }
    }
}



