import {
    Component, OnInit, trigger, state, style, transition,
    animate, Directive, Input, ElementRef, ViewChild, OnDestroy,
    HostListener, AfterViewInit
} from '@angular/core';
//import {Http} from '@angular/http';
import {Observable} from 'rxjs/Observable';
//import "rxjs/add/observable/interval";
//import "rxjs/add/operator/map";
import {NgbPanelChangeEvent} from '@ng-bootstrap/ng-bootstrap';
import {MotosrvService} from '../motosrv.service';
import {HdmotosrvService} from '../hdmotosrv.service';
import {Moto} from '../moto';
import {Hdmoto} from '../hdmoto';

import * as d3 from 'd3';
import * as dc from 'dc';
//import * as myLib from 'myLib';

import * as crossfilter from 'crossfilter';

declare var MyLib: any;
//declare var moto: any;
//declare function myLib(): any;

@Component({
    selector: 'app-charts',
    templateUrl: './charts.component.html',
    styleUrls: ['./charts.component.css'],
    animations: [
        trigger('slideInOut', [
            state('in', style({
                transform: 'translate3d(0, 0, 0)'
            })),
            state('out', style({
                transform: 'translate3d(100%, 0, 0)'
            })),
            transition('in => out', animate('400ms ease-in-out')),
            transition('out => in', animate('400ms ease-in-out'))
        ]),
    ]
})

export class ChartsComponent implements OnInit, OnDestroy, AfterViewInit {

    mlabel = 'Комплекс СМД';
    tmpmlabel = 'Комплекс СМД';
    option_val: any;

    types = ['КСМД', '2', '3', '4', '5', 'Аспирация КСМД'];

    @ViewChild('inner') el: ElementRef;
    @ViewChild('outer') elo: ElementRef;
    @ViewChild('ch1') ch1: ElementRef;
    @ViewChild('ch2') ch2: ElementRef;
    @ViewChild('ch3') ch3: ElementRef;
    @ViewChild('#prof') prof1: ElementRef;

    menuState: string = 'out';
    innertop: number = 100;
    innertops: string = '85px';

    chart1: any;
    chart2: any;
    chart3: any;
    chart5: any;
    chart6: any;
    chart7: any;
    chart8: any;
    chart9: any;
    chart10: any;
    chart11: any;
    chart12: any;
    chart14: any;

    mLib: any;

    itm: number;
    it_m: number;
    s: any;
    motodata: any;
    tfilt: any;
    mLibObj: any;
    isopen: any;
    chs1w: any;
    chs2w: any;
    chs3w: any;
    idx: any;
    mlib: any;

    w_old1: any
    w_old2: any
    w_old3: any

    motos: any;
    equips: any[];
    tmpequip: any[] = [];

    constructor(private motosrvService: MotosrvService,
        private hdmotosrvService: HdmotosrvService) {

        this.itm = 0;
        this.it_m = 0;
        this.s = "constr.start";
        this.tfilt = 1;
        this.isopen = false;
        this.idx = 0;
    }

    ngAfterViewInit() {
    }

    ngOnInit() {

        this.motos = this.motosrvService.getData();
        this.equips = this.hdmotosrvService.getStruct();

        //console.log(this.motos);

        this.mlib = new MyLib();
        this.mlib.init(this.motos);
        this.getCf();

        this.w_old1 = -1;

        /*        this.http.get('moto.json')
                    .map((res) => res.json())
                    .subscribe((res) => {
                        this.mlib = new MyLib(res);
                        this.mlib.init();
                        this.getCf();
                    });
        */
        /*        setTimeout(() => {
                    this.innertop = $(this.el.nativeElement).offset().top;
                    console.log($(this.el.nativeElement).offset().top);
                    console.log(this.elo);

                }, 2);
        */

        /*        window.onload = function () {
                    alert('Документ и все ресурсы загружены');
                    // alert(this.ch1.nativeElement);
                };
        */
    }

    getCf() {
        var w1 = 0, w2 = 0, w3 = 0;

        if ($("#charts").width() > 0) {
            w1 = $("#charts").width();
            if (w1 < 902) {
                w2 = w1;
                w3 = w1;
            } else {
                w2 = w1 / 1.5;
                w3 = w1 - w2;
            }
        }
        if ($("#tables").width() > 0) {
            w1 = $("#tables").width();
            if (w1 < 902) {
                w2 = w1;
                w3 = w1;
            } else {
                w2 = w1 / 1.5;
                w3 = w1 - w2;
            }
        }

        this.chart1 = this.mlib.drawChart1(dc.barChart("#test1"), w1);
        this.chart2 = this.mlib.drawChart2(dc.compositeChart("#test2"), w2);
        this.chart1 = this.mlib.focusOn(this.chart1, this.chart2);
        this.chart1.filter(null);
        this.chart3 = this.mlib.drawChart3(dc.compositeChart("#test3"), w3);

        this.chart5 = this.mlib.drawChart5(dc.numberDisplay("#test5"));
        this.chart6 = this.mlib.drawChart6(dc.numberDisplay("#test6"));
        this.chart7 = this.mlib.drawChart7(dc.numberDisplay("#test7"));
        this.chart8 = this.mlib.drawChart8(dc.numberDisplay("#test8"));
        this.chart9 = this.mlib.drawChart9(dc.numberDisplay("#test9"));
        this.chart10 = this.mlib.drawChart10(dc.numberDisplay("#test10"));
        this.chart11 = this.mlib.drawChart11(dc.numberDisplay("#test11"));
        this.chart12 = this.mlib.drawChart12(dc.numberDisplay("#test12"));
        this.chart14 = this.mlib.drawChart14(dc.numberDisplay("#test14"));

        dc.renderAll();
    }

    cLog() {
        //      console.log(this.s);
    }

    onBt1(event) {
        this.mlib.fltData(1);
        this.chart2 = this.mlib.setYScale(this.chart2);
        dc.redrawAll();
    }

    onBt2(event) {
        this.mlib.fltData(2);
        this.chart2 = this.mlib.setYScale(this.chart2);
        dc.redrawAll();
    }

    onBt3(event) {
        this.mlib.fltData(3);
        this.chart2 = this.mlib.setYScale(this.chart2);
        dc.redrawAll();
    }

    onBt4() {
        var flt: any;
        this.motos = this.motosrvService.getNewData();
        //alert(this.motos);
        //console.log(this.motos);
        this.mlib.reInit(this.motos);
        this.mlabel = this.tmpmlabel;

        ///flt = this.chart1.filters();
        this.chart1.filter(null);
        this.mlib.setItem(this.it_m);

        this.chart1 = this.mlib.setDomain(this.chart1);
        this.chart2 = this.mlib.setDomain(this.chart2);
        this.chart2 = this.mlib.setYScale(this.chart2);

        //this.chart2 = this.mlib.setYMax(this.chart2);
        //this.chart1 = this.mlib.focusOn(this.chart1, this.chart2);

        dc.redrawAll();
        //    console.log("onBt4 " + this.it_m);
    }

    onLoad(event) {
        console.log("onLoad");
    }

    onResize(event) {

        var w1 = 0, w2 = 0, w3 = 0;

        if ($("#charts").width() > 0) {
            w1 = $("#charts").width();
            if (w1 < 885) {
                w2 = w1;
                w3 = w1;
            } else {
                w2 = w1 / 1.5;
                w3 = w1 - w2;
            }
        }
        if ($("#tables").width() > 0) {
            w1 = $("#tables").width();
            if (w1 < 885) {
                w2 = w1;
                w3 = w1;
            } else {
                w2 = w1 / 1.5;
                w3 = w1 - w2;
            }
        }

        this.mlib.resizeChart(this.chart1, w1);
        this.mlib.resizeChart(this.chart2, w2);
        this.mlib.resizeChart(this.chart3, w3);

        //console.log("win resized!!! w = " + w1);
    }

    getStage(value) {

        var arr: any[];
        this.tmpequip.length = 0;

        if (value > 0) {
            arr = this.equips[value - 1];
            for (let j = 0; j < arr.length; j++) {
                this.tmpequip.push(arr[j]);
            }
            this.it_m = this.tmpequip[0].item;
            this.tmpmlabel = 'Комплекс СМД: ' + this.tmpequip[0].stage + '.' +
                this.tmpequip[0].substage + ' (' + this.tmpequip[0].name + ')';
        } else {
            this.it_m = 0;
            this.tmpmlabel = 'Комплекс СМД';
        }
        //console.log(value, this.it_m, this.tmpequip);
    }

    getSubStage(value) {
        this.it_m = this.tmpequip[value].item;
        this.tmpmlabel = 'Комплекс СМД: ' + this.tmpequip[value].stage + '.' +
            this.tmpequip[value].substage + ' (' + this.tmpequip[value].name + ')';

        //console.log(value, this.it_m, this.tmpequip);
    }

    ngOnDestroy() {
        this.mlib = null;
    }

    toggleMenu() {
        this.menuState = this.menuState === 'out' ? 'in' : 'out';
        //    console.log(this.mlib);

    }

    getTop() {
    }

    /*    @HostListener("mouseenter") onMouseEnter() {
            console.log("-charts = " + $("#charts").width());
            console.log("_tables = " + $("#tables").width());
        }
    */

}



