import {BrowserModule} from '@angular/platform-browser';
import {NgModule} from '@angular/core';
import {FormsModule} from '@angular/forms';
import {HttpModule} from '@angular/http';

import {NgbModule} from '@ng-bootstrap/ng-bootstrap';
import {AppComponent} from './app.component';
import {ChartsComponent} from './charts/charts.component';
import {TablesComponent} from './tables/tables.component';
import {TmenuComponent} from './tmenu/tmenu.component';
import {CmenuComponent} from './cmenu/cmenu.component';
import {MotosrvService} from './motosrv.service';
import {HdmotosrvService} from './hdmotosrv.service';
import {HttpService} from './http.service';

@NgModule({
    declarations: [
        AppComponent,
        ChartsComponent,
        TablesComponent,
        TmenuComponent,
        CmenuComponent
    ],
    imports: [
        NgbModule.forRoot(),
        BrowserModule,
        FormsModule,
        HttpModule
    ],
    providers: [MotosrvService, HdmotosrvService, HttpService],
    bootstrap: [AppComponent]
})
export class AppModule {}



