import {Injectable} from '@angular/core';
import {Hdmoto} from './hdmoto';
import * as crossfilter from 'crossfilter';

declare var hdmoto: any;

@Injectable()
export class HdmotosrvService {

    private data: Hdmoto[];
    private equipStruct: any[] = [];

    private hdmotosout: any[] = [];
    private items: any[] = [];

    constructor() {
        this.initData();
    }

    initData() {
        var arr: any[];

        this.data = hdmoto;

        this.items = this.getDataItems();
        for (let i = 0; i < this.data.length; i++) {
            this.hdmotosout.push(this.data[i]);
        }

        this.equipStruct.push(this.getEqByStage(2));
        this.equipStruct.push(this.getEqByStage(3));
        this.equipStruct.push(this.getEqByStage(4));
        this.equipStruct.push(this.getEqByStage(5));
        arr = this.getEqByStage(8);
        for (let i = 0; i < arr.length; i++) {
            arr[i].stage = 'a';
        }
        this.equipStruct.push(arr);
    }

    getStruct(): any[] {
        return this.equipStruct;
    }

    getData(): Hdmoto[] {
        return this.hdmotosout;
        //return this.data;
    }

    chkMot(chkNmb) {
        this.items[chkNmb] = !this.items[chkNmb];
        this.getOut();
    }

    getOut() {
        this.hdmotosout.length = 0;
        for (let i = 0; i < this.data.length; i++) {
            if (this.items[i]) {
                this.hdmotosout.push(this.data[i]);
            }
        }
    }

    getDataItems(): boolean[] {
        var arr: boolean[] = [];
        for (let i = 0; i < this.data.length; i++) {
            arr.push(true);
        }
        return arr;
    }

    setMoto(_i, _m, _dtf, _dtl) {
        hdmoto[_i].dtfirst = _dtf;
        hdmoto[_i].dtlast = _dtl;
        hdmoto[_i].moto[0] = Math.round(_m[0] * 100) / 100;
        hdmoto[_i].moto[1] = Math.round(_m[1] * 100) / 100;
        hdmoto[_i].moto[2] = Math.round(_m[2] * 100) / 100;
        hdmoto[_i].lmoto[0] = Math.round(_m[3] * 100) / 100;
        hdmoto[_i].lmoto[1] = Math.round(_m[4] * 100) / 100;
        hdmoto[_i].lmoto[2] = Math.round(_m[5] * 100) / 100;
    }

    getStageList(): any[] {
        var _cf = crossfilter(this.data);
        var _stageDimension = _cf.dimension(function (d) {return d.stage;});
        var _stageGrouping = _stageDimension.group();
        //var _countMeasure = _stageDimension.group();//.reduceCount();
        //var _ret = _countMeasure.top(5);
        var _ret = _stageGrouping.all();

        return _ret;
    }

    getEqByStage(st): any {
        var _cf = crossfilter(this.data);
        var _stageDimension = _cf.dimension(function (d) {return d.stage;});
        var _substageDimension = _cf.dimension(function (d) {return d.substage;});
        var _itemDimension = _cf.dimension(function (d) {return d.item;});
        _substageDimension.filter(function (d) {return d !== '0'});
        _stageDimension.filter(st);

        return _itemDimension.bottom(Infinity);
    }

}


